import { useState } from 'react'
import styles from './AboutBlock.module.scss'

const AboutBlock = props => {
    const [showFull, setShowFull] = useState(false)

    const showMoreHandler = (e) => {
        setShowFull(true);
        e.target.remove();
    }

    const contentClasses = [styles.AboutBlock__content];
    if (!showFull) contentClasses.push(styles.AboutBlock__content__hidden);
    return (
        <section className={styles.AboutBlock}>
            <div className={contentClasses.join(' ')}>
                <h2 className={styles.AboutBlock__title}>   </h2>
                <div className={styles.AboutBlock__description}>
                    <p> -   ?    ,      ,     .  ? .       !       Dominos Pizza!    ,  Dominos Pizza   :      ;  ,  ,    ,     ;        30 ,        .</p>
                    <h3>  </h3>
                    <p>   Dominos          ,   .            !    ,  :      ;    ;   ,                Dominos.      !</p>
                </div>
            </div>
            <div className={styles.AboutBlock__showMore} onClick={showMoreHandler}> </div>
        </section>
    )
}

export default AboutBlock